import React from 'react';
import './caro.css';

function AppCarousel() {
    return (
        <div>
            <div id="carouselExampleCaptions" className="carousel slide" data-ride="carousel">
                <ol className="carousel-indicators">
                    <li data-target="#carouselExampleCaptions" data-slide-to="0" className="active"></li>
                    <li data-target="#carouselExampleCaptions" data-slide-to="1"></li>
                    <li data-target="#carouselExampleCaptions" data-slide-to="2"></li>
                </ol>
                <div className="carousel-inner">
                    <div className="carousel-item active">
                        <img src="https://stillmed.olympic.org/media/Images/OlympicOrg/News/2019/12/11/2019-12-11-mountain-day-featured-01.jpg" height="640vh" className="d-block w-100" alt="..." />
                        <div className="carousel-caption d-none d-md-block">
                            <h5>Queen of Hills</h5>
                            <p>Shimla is the capital of the northern Indian state of Himachal Pradesh, in the Himalayan foothills.</p>
                        </div>
                    </div>
                    <div className="carousel-item">
                        <img src="https://www.nationalgeographic.com/content/dam/animals/rights-exempt/2019-travel-photo-contest/prod-yourshot-1791825--2-.jpg" height="640vh" className="d-block w-100" alt="..." />
                        <div className="carousel-caption d-none d-md-block">
                            <h5>Flora and Fauna</h5>
                            <p>A green pearl in the Himalayan crown, Himachal Pradesh is blessed with a rich flora and fauna that graces the land with grandeur and majesty</p>
                        </div>
                    </div>
                    <div className="carousel-item">
                        <img src="https://c1.wallpaperflare.com/preview/868/31/761/chandra-tal-spiti-himachal-pradesh-india.jpg" height="640vh" className="d-block w-100" alt="..." />
                        <div className="carousel-caption d-none d-md-block">
                            <h5>Sustainability of Traditional Drinking Water Sources</h5>
                            <p>Water is one of the most vital natural resources of Himachal Predesh.</p>
                        </div>
                    </div>
                </div>
                <a className="carousel-control-prev" href="#carouselExampleCaptions" role="button" data-slide="prev">
                    <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                    <span className="sr-only">Previous</span>
                </a>
                <a className="carousel-control-next" href="#carouselExampleCaptions" role="button" data-slide="next">
                    <span className="carousel-control-next-icon" aria-hidden="true"></span>
                    <span className="sr-only">Next</span>
                </a>
            </div>


        </div>
    )
}

export default AppCarousel;
